	module.exports = {"data":{"projectsJson":{"name":"ForceField","keywords":["Embedded Systems","Hardware","VR"],"status":["complete","green"],"tagline":"A cable-driven force feedback computer mouse for use in haptic/tactile virtual reality, fine motor skill rehabilitation, and 3d computer aided design.","year":[2015],"description":"*This was my senior design project in college.*\n\nForceField is a cable-driven force feedback computer mouse for use in haptic/tactile virtual reality, fine motor skill rehabilitation, and 3d computer aided design. ForceField allows you to create and simulate virtual objects, textures, and forces in your desktop environment.\n\nThe project was *super successful* and we ended up **winning the Frederick Ketterer Memorial Award for outstanding creativity in Engineering Design** as well as 4th place overall in the school of engineering. Tyler and Preston did the majority of the mechanical engineering, Colin did the computer-side software, and **I took lead on the electrical engineering aspects as well as the microcontroller-side software.**\n\nFor more information or access to any of the design files, shoot me an email at davvidbaker@gmail.com, and I'd be happy to share. I think I know where they are.","images":["forcefield.JPG"],"videos":null,"id":"/Users/davidbaker/code/davidbaker.is/src/data/projects.json absPath of file [13] >>> JSON","logo":null,"link":"https://www.youtube.com/watch?v=5L02NgFlN2I&feature=youtu.be","linkToSource":null,"linkToTrello":null,"callToAction":"Watch presentation","teammates":[{"name":"Colin Feo","link":"https://www.linkedin.com/in/colinfeo/"},{"name":"Preston Morris","link":"https://www.linkedin.com/in/prestonjmorris/"},{"name":"Tyler Caron","link":"https://www.linkedin.com/in/tyler-caron-a54bb852/"}],"agency":null}},"pathContext":{"projectPath":"a-haptics-engineer"}}

